//stylistic
.tile.text {
		
	.title {
     	line-height: 120%;
    	padding: 0;
    	color: @primary-color;
    	.component-title;
	}

	.body {
		line-height: 120%;
		padding: 20px 0 0 0;
		color: @secondary-color;
	}

	.instruction {
		line-height: 120%;
		padding: 20px 0 10px 0;
		color: @secondary-color;
		.instruction-text;
	}

}


//structural
.tile.text {

	.text {
		z-index:1;
	}

	.title {
    	position: relative;
	}

	.body {
		position: relative;
	}

	.instruction {
		position: relative;
	}

	.background {
		position:absolute;
		top:0px;
		left:0px;
		bottom:0px;
		right:0px;
	}


	&[textrounded="true"] {

		.background {
			border-radius: 8px 8px 8px 8px;
		}

	}
}