html.lightbox-hack {
    position: absolute;
    height:100%;
    width: 100%;
    overflow-y: hidden;
    overflow-x: hidden;

    body {
        height: 100% !important;
        overflow-y: hidden;
    }

    .scrolling-container {
        height:100%;
        overflow-y: scroll;
        -webkit-overflow-scrolling: touch;
    }

    .navigation-container {
        position: fixed;
        overflow-y: scroll;
        z-index: 500;
        width: 100%;
        .navigation {
            position:relative !important;
        }
    }
}



.lightbox-container {

    // &.lightbox-display-none {
    //     left:100%;
    // }

    position: fixed;
    .lightbox-hack & {
    	position: absolute;
    }
	top: 100%;
	left: 0;
	width: 100%;
    height:100%;
    overflow-y: auto;
    overflow-x: hidden;
    -webkit-overflow-scrolling: touch;
    z-index:540;
    background-color: @transparency;

    .no-opacity & {
        background-image: @transparency-fallback-png;
    }

    .lightbox-popup-container {
        position: relative;
    }

    .lightbox-popup {
        z-index:540;

        max-width: 100%;
        overflow: hidden;
        position: relative;
        &.has-background {
            .block {
                background-color: transparent;
            }
            .article {
                background-color: transparent;
            }
        }

        .lightbox-popup-inner {
            position:relative;
            max-width: 1024px;
            margin: auto;
            background-color: white;
        }

        #a11y-focusguard {
            position:absolute !important;
            bottom: 0px !important;
        }
    }
    .lightbox-popup.lightbox-popup-background {
        position: absolute;
        width: 100%;
        height: 100%;
        overflow: hidden;
        .lightbox-popup-inner {
            min-height: 100%;
        }
    }

    .article-block-container {
        max-width: 100%;
        position:relative;

    }

    .close-button {
        position: absolute;
        top: 0px;
        right: 0px;
    }

    .background-image {
        position:absolute;
        top:0px;
        left:0px;
        overflow:hidden;
        width: 100%;
        img {
            max-width: none;
            position:absolute;
        }
    }

    &.fullscreen {
        bottom: 0;
        height: 100%;
        top: 0 !important;
    }

    .lightbox-popup {
        height: 100%;
    }
    .lightbox-popup-inner {
        height: 100%;
    }

    .background-image {
        height: 100%;
    }

    &.has-background {
        .block, .article {
            background-color: transparent;
        }
    }
}