.tile {
    
    &.image[data-link] {

            cursor: pointer;
            .no-touch &:hover {
                .icon-default {
                    display: none;
                }
                .icon-hover {
                    display: block;
                }
            }

    }

    &.text[data-link] {

            cursor: pointer;
            .no-touch &:hover {
                .icon-default {
                    display: none;
                }
                .icon-hover {
                    display: block;
                }
            }

    }

    .lightbox-link {
        line-height: 100%;
        display: inline-block;

        .lightbox-icon {
            position:relative;
            padding: 0px;
            height:40px;
            width:40px;
            background-color: transparent;
            .icon-default {
                display: block;
            }
            .icon-hover {
                display: none;
            }
        }
    }
    .lightbox-link-title {
        
    }
    .title-text {
        display: inline-block;
    }
    .lightbox-link-center {
        hr {
            width: 12%;
            left: 44%;
            height: 5px;
            color: @primary-color-inverted;
            background-color: @primary-color-inverted;
        }
        .lightbox-link-instruction {
            display: inline-block;
            width: 120px;
            font-weight: bold;
            color: @primary-color-inverted;
        }
    }

    .lightbox-link-progress {
        a {
            text-decoration: inherit;
            color: inherit;
        }
    }
}


.tile {
    .lightbox-link-center {
        position: absolute;
        top: 22%;
        margin-top: -28px;
        left: 0px;
        width: 100%;
        text-align: center;

        hr {
            position: relative;
            border: none;
        }
    }
    .lightbox-link-progress-container {
        height: 16px;
        padding-top: 10px;
        .lightbox-link-progress {
            position: absolute;
            right: 16px;
            bottom: 16px;
        }
    }
}